body {
  font-family: 'Inconsolata', monospace;
  font-size: 16px;
  margin: 0;
  padding: 0;
  text-align: center;
}
*:focus {
  outline: none;
}
#timer, #anotherTimer {
  -ms-flex-align: end;
      align-items: flex-end;
  -webkit-box-shadow: -3px 3px 10px #c1c1c1;
          box-shadow: -3px 3px 10px #c1c1c1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: auto;
  padding: 50px;
  width: 300px;
}
#anotherTimer {
  margin-top: 100px;
}

.input-field-wrap {
  position: relative;
  width: 100%;
}
.input-field-wrap .hint {
  color: #c5c5c5;
  font-style: italic;
}
.input-field-wrap .inputs-wrap {
  margin-bottom: 10px;
  text-align: left;
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transition: -webkit-transform .2s ease-in-out;
  transition: -webkit-transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.input-field-wrap .inputs-wrap.hide-inputs {
  -webkit-transform: rotateX(90deg);
          transform: rotateX(90deg);
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
.input-field-wrap 
.inputs-wrap 
input {
  border: none;
  border-bottom: 1px solid;
  font-size: 1.5rem;
  text-align: center;
  width: 30px;
}

::-webkit-input-placeholder {
  color: #c5c5c5;
  font-style: italic;
}

:-ms-input-placeholder {
  color: #c5c5c5;
  font-style: italic;
}

::-ms-input-placeholder {
  color: #c5c5c5;
  font-style: italic;
}

::placeholder {
  color: #c5c5c5;
  font-style: italic;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}
input:focus:-ms-input-placeholder {
  color: transparent;
}
input:focus::-ms-input-placeholder {
  color: transparent;
}
input:focus::placeholder {
  color: transparent;
}

.input-field-wrap .progress-bar {
  background-color: #e8e8e8;
  border-radius: 20px;
  bottom: 0;
  height: 50%;
  left: 0;
  position: absolute;
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transition: -webkit-transform .2s ease-in-out;
  transition: -webkit-transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
  width: 100%;
}
.input-field-wrap .progress-bar.hide-bar {
  -webkit-transform: rotateX(-90deg);
          transform: rotateX(-90deg);
}
.input-field-wrap .progress-bar .line {
  border-radius: 20px;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  -webkit-transition: background-color .2s ease-in-out;
  -o-transition: background-color .2s ease-in-out;
  transition: background-color .2s ease-in-out;
  width: 100%;
}


.time-field-wrap {
  margin-top: 20px;
}
.time-field-wrap {
  -ms-flex-align: baseline;
      align-items: baseline;
}
.time-field-wrap span {
  display: inline-block;
  font-size: 3rem;
  text-align: center;
  width: 45px;
}
.time-field-wrap span.colon {
  width: 20px;
}
.time-field-wrap span.ms {
  font-size: 1.5rem;
  margin-left: 10px;
}



.button-field-wrap {
  margin-top: 20px;
  text-align: center;
  width: 100%;
}
.button-field-wrap button {
  border: none;
  border: 1px solid #0099dc;
  border-radius: 3px;
  font-size: .75rem;
  cursor: pointer;
  height: 25px;
  text-transform: uppercase;
  width: 35px;
}
.button-field-wrap button:active {
  -webkit-transform: scale(.95);
      -ms-transform: scale(.95);
          transform: scale(.95);
}
.button-field-wrap button.start {
  background: url(svg/start.svg) center center/ 90% 58% no-repeat #0099dc;
}

.button-field-wrap button.pause {
  background: url(svg/pause.svg) center center/ 90% 58% no-repeat #0099dc;
}
.button-field-wrap button.stop {
  background: url(svg/stop.svg) center center/ 90% 58% no-repeat;
  margin-left: 15px;
}



.sound-field-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.sound-field-wrap .select-field-wrap,
.sound-field-wrap .check-field-wrap {
  margin-top: 30px;
  position: relative;
}
.sound-field-wrap button {
  cursor: pointer;
  height: 100%;
}
.sound-field-wrap .select-field-wrap button.choose {
  background-color: #fff;
  border: 1px solid #555;
  border-radius: 3px 3px 0 0;
  color: #555;
  width: 130px;
}

.sound-field-wrap .select-field-wrap ul.select-sound {
  background-color: #fff;
  border: 1px solid #555;
  border-top: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  box-sizing: border-box;
  left: 0;
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  text-align: left;
  width: 100%;
  z-index: 100;
}
.sound-field-wrap .select-field-wrap ul.select-sound.close {
  display: none;
}
.sound-field-wrap button,
.sound-field-wrap .select-field-wrap ul.select-sound li a {
  font-size: 16px;
  padding: 4px 10px 4px;
}
.sound-field-wrap .select-field-wrap ul.select-sound li a {
  color: #777;
  display: block;

  text-decoration: none;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; 
}
.sound-field-wrap .select-field-wrap ul.select-sound li a:hover {
  background-color: #309dce;
  color: #fff;
}

.sound-field-wrap .check-field-wrap button.play,
.sound-field-wrap .check-field-wrap button.stop {
  border: none;
  border-radius: 3px;
  color: #fff;
  margin-left: 30px;
  /*transition: background-color .1s ease-in-out;*/
  width: 130px;
}

.sound-field-wrap .check-field-wrap button.stop {
  background-color: #ff6d6d;
}

.sound-field-wrap .check-field-wrap button.play {
  background-color: #309dce;
}



